/* ================= GRID CARD ================= */
.event-card {
  background: #fff;
  border-radius: 18px;
  padding: 14px;
  display: flex;
  flex-direction: column;
}

/* IMAGE FIX (MAIN ISSUE SOLVED HERE) */
.card-img-wrapper {
  position: relative;
  overflow: hidden;
  border-radius: 14px;
}

.card-img-wrapper img {
  width: 100%;
  height: 180px;          /* ðŸ”¥ forces landscape */
  object-fit: cover;     /* ðŸ”¥ crop portrait/square */
  display: block;
}

/* STATUS BADGE */
.status-badge {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 12px;
  padding: 4px 10px;
  border-radius: 20px;
  background: #eee;
  font-weight: 600;
}

.status-badge.approved { background: #e3f2fd; }
.status-badge.draft { background: #e8f5e9; }
.status-badge.completed { background: #ede7f6; }
.status-badge.pending { background: #fff3cd; }
.status-badge.rejected { background: #f8d7da; }

/* BODY */
.event-body {
  padding-top: 14px;
  flex: 1;
}

.event-body h6 {
  font-weight: 600;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 6px;
}

/* ACTIONS */
.event-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.edit-btn {
  border: 1px solid #7a12ff;
  color: #7a12ff;
  background: transparent;
  border-radius: 20px;
  padding: 4px 14px;
  font-size: 13px;
}

.menu-btn {
  border: none;
  background: none;
  font-size: 20px;
  cursor: pointer;
}

/* ================= LIST VIEW ================= */
.list-card {
  display: flex;
  align-items: center;
  gap: 16px;
  background: #fff;
  padding: 14px;
  border-radius: 14px;
  margin-bottom: 12px;
}

.list-img {
  width: 120px;
  height: 70px;
  object-fit: cover;     /* ðŸ”¥ landscape crop */
  border-radius: 10px;
  flex-shrink: 0;
}

.list-info {
  flex: 1;
}

.list-info h6 {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.list-actions {
  display: flex;
  align-items: center;
  gap: 10px;
}
